import React from "react";
import "./circle.css";
import "./LandingPage.css";
import { useState, useEffect } from "react";
import First from "./FirstPage";
import Second from "./SecondPage";
import Third from "./ThirdPage";
import Fourth from "./FourthPage";

const Main = () => {
  const [circle] = useState(4);
  let [count, setcount] = useState(1);
  const [active, setactive] = useState(1);
  const [width, setwidth] = useState(1);

  useEffect(() => {
    if (active === 1) setwidth(25);
    if (active === 2) setwidth(50);
    if (active === 3) setwidth(80);
    if (active === 4) setwidth(100);
  }, [circle, active]);

  const arr = [];
  for (let i = 0; i < circle; i++) {
    arr.push(
      <div className={i < active ? "active" : "circle"} key={i}>
        {i + 1}
      </div>
    );
  }

  const Change = (active) => {
    setactive(active + 1);
    setcount(count + 1);
  };

  return (
    <div>
      <div className="div-1">
        <img
          src="data:image/png;base64,"
          alt="img-1"
          width="32px"
          height="34px"
        ></img>
        <h2>Eden</h2>
      </div>
      <div className="container">
        <div className="content">
          <div className="progressbar">
            <div className="progress" style={{ width: width + "%" }}></div>
            {arr}
          </div>
        </div>
      </div>
      <form className="form">
        {count === 1 ? <First /> : ""}
        {count === 2 ? <Second /> : ""}
        {count === 3 ? <Third /> : ""}
        {count === 4 ? <Fourth /> : ""}
      </form>
      <div
        className="btn-1"
        onClick={() => {
          active >= circle ? setactive(circle) : Change(active);
        }}
      >
        Create Workspace
      </div>
    </div>
  );
};

export default Main;
